import React from 'react';

const About = () => {
  return (
    <div className='content-wrapper' id="about">
        <div className="about-wrapper">
            <div className="about-poster">
               <img src="static/img/about.gif" alt=""/>
            </div>
            <div className="about-content">
                <h2>
                    <span className='about-heading'>
                        About me
                    </span>
                </h2>
                <h3>
                    <span className='content-text-subheading'>
                        Everything started width passion to web tehnologies. <br/><br/>
                        <div className="content-text-item">
                             2016 - I got my first commercial works as a <i>front-end developer</i> in Mooldstarter agency.
                        </div>
                        <div className="content-text-item">
                             2017 - I started working as a <i>back-end developer</i> on  freelance platforms.
                        </div>
                        <div className="content-text-item">
                             2018/07 - I worked as a <i>front-end developer</i> at Cherry Digital agency.
                        </div>
                        <div className="content-text-item">
                            Rest of my experience is cooperation as a freelancer/self employed with IT companies and professionals from Moldova,Europe.
                        </div>
                    </span>
                </h3>
            </div>
        </div>
        <style jsx>{`
          .about-heading{
              color:#5A81ED;
              font-weight:600;
              font-size:2.5rem;
          }
          .about-poster{
            position: relative;
          }
          .about-content{
              max-width: 470px;
          }
          .about-poster{
              align-self: center;
          }
          .about-poster img{
              max-width: 420px;
              z-index:0;
              border-top-left-radius: 50%;
          }
          .about-wrapper{
              display:flex;
              justify-content:space-between;
              margin-bottom: 150px;
          }
         @media (max-width:845px){
             .about-poster{
                 display: none;
             }
             .about-wrapper{
                 justify-content: center;
                 margin-bottom: 80px;
             }
             h2{
                 text-align:center;
             }
             
         }
        `}</style>
    </div>
  )
}

export default About
